<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        товаров в корзине: {{ $store.state.cartProducts.length }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">

        <div class="cart__field" v-if="formLoading">
          <img width="50%" src="../assets/Spinner-3.gif" alt="">
        </div>

        <!-- <div class="cart__field" v-else-if="!formError"></div> -->

        <div class="cart__field" v-else>
          <div class="cart__data">

            <BaseFormText v-model="formData.name" title="ФИО" :error="formError.name"
              placeholder="Введите ваше полное имя" />

            <BaseFormText v-model="formData.address" title="Адрес доставки" :error="formError.address"
              placeholder="Введите ваш адрес" />

            <BaseFormText v-model="formData.phone" title="Телефон" type="tel" :error="formError.phone"
              placeholder="Введите ваш телефон" />

            <BaseFormText v-model="formData.email" title="Email" type="email" :error="formError.email"
              placeholder="Введи ваш Email" />

            <BaseFormTextarea title="Комментарий к заказу" v-model="formData.comment" :error="formError.comment"
              placeholder="Ваши пожелания" />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>{{ deliveryPrice }} ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <CartOrder v-for="item in products" :key="item.productId" :item="item" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ deliveryPrice }} ₽</b></p>
            <p>Итого: <b>{{ $store.state.cartProducts.length }}</b> товара на сумму <b>{{ PricePretty }}</b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>

        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import CartOrder from '@/components/CartOrder.vue';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';


export default {
  components: { BaseFormText, BaseFormTextarea, CartOrder },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    PricePretty() {
      return numberFormat(this.totalPrice)
    },
    deliveryPrice() {
      return numberFormat(this.$store.state.deliveryPrice);
    },
  },

  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formLoading: false,
      FormLoadingFailed: false
    }
  },
  methods: {
    order() {
      this.formLoading = false;
      this.formLoadingFailed = true;
      this.formError = {};
      this.formErrorMessage = '';
      clearTimeout(this.loadFormTimer);
      this.loadFormTimer = setTimeout(() => {

        axios
          .post(API_BASE_URL + '/api/orders', {
            ...this.formData
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          })
          .then((response) => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({name: 'orderinfo', params: {id: response.data.id}});
          })
          .catch(error => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .catch(() => this.formLoadingFailed = false)
          .then(() => this.formsLoading = true);


      }, 0);


    }

  },

}


</script>