<template>
    <li v-bind="$attrs" v-for="product in productsNormolized" :key="product.id">
        <router-link class="catalog__pic" :to="{name:'product', params: {id: product.id}}">
            <img :src="product.image" :alt="product.title">
        </router-link>

        <h3 class="catalog__title">
            <a href="#" @click.prevent="openQuickView(product.id)">
                {{ product.title }}
            </a>
        </h3>

        <span class="catalog__price">
            {{ product.PricePretty }} ₽
        </span>

        <ul class="colors colors--black">
            <li class="colors__item" v-for="color in product.colors" :key="color.id">
                <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio"
                    :value="color.id" :id="color.id">
                    <span class="colors__value" :style="{background: color.code}">
                    </span>
                </label>
            </li>
        </ul>
    </li>

    <BaseModal v-model:open="isQuickViewOpen">
        <ProductQuickView :product-id="currentProductId"/>
    </BaseModal>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import BaseModal from '@/components/BaseModal.vue';
import { defineAsyncComponent, h } from 'vue';

export default {
    inheritAttrs: false,
    components: { 
        BaseModal,
        ProductQuickView: defineAsyncComponent({
            loader: () => import('@/components/ProductQuickView.vue'),
            delay: 0,
            loadingComponent: () => h('div', 'Загрузка...')
        })
    },
    data() {
        return {
            color: '#73B6EA',
            currentProductId: null
        };
    },
    computed: {
        isQuickViewOpen: {
            get() {
                return !!this.currentProductId;
            },
            set(isOpen) {
                if (!isOpen) {
                    this.currentProductId = null;
                }
            }
        },
        productsNormolized() {
            return this.products.map((product) => {
                return {
                    ...product,
                    PricePretty: numberFormat(product.price)
                }
            });
        },

    },
    methods: {
       gotoPage,
       openQuickView(productId) {
        this.currentProductId = productId;
       },
    },
    props: ['products'],
}
</script>